import { useState, useEffect } from 'react';

import { useFirebase } from "@meddbriefer/mdb-firebase"
import { getLog, getPublishedScenarioObjectByID } from '@meddbriefer/scenario-data'
import { analyzeEvents, requiredPhaseNames, saveAnalysisLog, c2FB, constraintsViolated} from '@meddbriefer/analysis'
import { currentUserName } from './MDBNavBar';
import DisplayContent from './DisplayContent';
import { assessmentEntries } from '@meddbriefer/feedback-data';


export default function Analyze({logID, context})  {
    const {db} = useFirebase()
    const [observerLog, setObserverLog] = useState (undefined)
    const [scenario, setScenario] = useState(undefined)
    const [analysisLog, setAnalysisLog] = useState(undefined)
    const [analysisActions, setAnalysisActions] = useState(undefined)
    const [loaded, setLoaded] = useState(false)

    
    /* function checkOnOverwriting() {
        if (context === "review"){
            analyzeEventsHook(scenario, observerLog)
        }
        else {if (window.confirm ("Are you sure you want to overwrite the existing analysis?  If you aren't sure: cancel, start a review and save it.  This will save a copy of the current analysis in a different directory."))
            analyzeEventsHook(scenario, observerLog)
        }
    } */

    async function analyzeEventsHook(scenario, log) {
      let actions = analyzeEvents(scenario, log)
      setAnalysisActions(actions)
      console.log(requiredPhaseNames)
      console.log(analysisActions)
            let metaData = {
      "label": log.label,
      "analyzer": currentUserName,
      "observerLogID": log.id,
      "FBfields": c2FB,
      "constraintsViolated": constraintsViolated,
      "requiredPhaseNames": requiredPhaseNames,
      "feedbackTemplates": assessmentEntries,
      "timestamp": Date()
      }
      
      let result = await saveAnalysisLog(db, metaData, actions)
      setAnalysisLog(result)
    }

    
    function processEvents ()  {
        analyzeEventsHook(scenario, observerLog)
        return (true)
    }

    useEffect(
        () => {
          getLog(db,logID)
            .then(observerLogData => {  
              setObserverLog(observerLogData)    
            })
            .catch(error => alert('error fetching observer log', error))
        // eslint-disable-next-line
      }, [db]
    )

      useEffect(
        () => {
          !!observerLog && getPublishedScenarioObjectByID(db, observerLog.scenarioID, "V2")
            .then(scenData => {
              console.log("retrieve scenario:", scenData)
              setScenario(scenData)
            })

        }, [db, observerLog]
      )

      useEffect(
        () => {
          !!scenario && processEvents()
        // eslint-disable-next-line
        }, [scenario]
      )


      useEffect(
        () => {
          !!analysisLog && setLoaded(true)
        }, [analysisLog]
      )

      if (!loaded) {
        return (<h3>Loading</h3>)
      }

    return (
      <DisplayContent
        analysisLog={analysisLog}
        actions={analysisActions}
        scenario={scenario}
        comments={null}
      />
    )
}
