import { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import { useFirebase } from "@meddbriefer/mdb-firebase"
import { getAnalysisLog, getObserverLog, getPublishedScenarioObjectByID } from "@meddbriefer/scenario-data"
import DisplayContent from './DisplayContent'

export default function InitializeForReview() {
  const {db} = useFirebase()
  const { logID } = useParams()
  const [ loaded, setLoaded ] = useState(null)
  const [ comments, setComments ] = useState(null)
  const [ actions, setActions ] = useState(null)
  const [ scenario, setScenario ] = useState(null)
  const [ analysisLog, setAnalysisLog ] = useState(null)
  //if there is a comment log for the input id then use that to get the
  //analysis log, otherwise the input logID is an analysis log
 
  
  useEffect(
    () => {
      getAnalysisLog(db, logID)
        .then(analysisLogData => {
          setActions(analysisLogData.events)
          setComments(Array(analysisLogData.events.length).fill(""))
          setAnalysisLog(analysisLogData)
          getObserverLog(db, analysisLogData.observerLogID)
            .then(observerLogData => {
              getPublishedScenarioObjectByID(db, observerLogData.scenarioID)
                .then(scenData => {
                  setScenario(scenData)

                })
            })
        })
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [logID]
  ) 

  

  useEffect(
    () => {
      !!scenario && setLoaded(true)
    },
   [scenario]
  )
 


  if (!loaded){
    return (<h3>Loading</h3>)
  }

  return (
    <DisplayContent
      analysis={analysisLog}
      actions={actions}
      scenario={scenario}
      comments={comments}
    />
  )

}

